.graphBlock {
  width: 90%;
  height: calc(100vh - 150px);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 100px;
}

.content {
  max-width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}